<template>
	<div class="modal fade" id="responseModal" tabindex="-1" role="dialog" aria-labelledby="responseModalTitle" aria-hidden="true">
		<div class="modal-dialog modal-lg" role="document">
			<div class="modal-content">
				<div class="modal-header modal__header">
					<h3 class="modal-title" id="responseModalTitle">Add Response</h3>
					<button type="button" class="close" data-dismiss="modal" aria-label="close">
						<span aria-hidden="true">&times;</span>
					</button>
				</div>

				<div class="modal-body modal__body">
					<form 
						@submit.prevent="createResponse"
						class="modal__form" 
						method="post">
						<div class="form-group">
							<label class="modal__label" for="class-title">Response</label>
							<input 
								type="text" 
								:class="[{'is-invalid': errors.response}]"
								class="form-control" 
								id="quiz-title" 
								placeholder="Response Title"
								@keydown="clearErrors($event.target.name)"
								v-model.trim="itemData.response">
							<div class="invalid-feedback">
								{{ errors.response }}
							</div>
						</div>
						<div class="form-group">
							<label class="modal__label" for="class-title">Remark</label>
							<quill-editor ref="myTextEditor"
								v-model="itemData.report"
								:options="editorOption">
							</quill-editor>
							<div class="invalid-feedback">
								{{ errors.report }}
							</div>
						</div>
					</form>
				</div>
				
				<div class="modal-footer modal__footer">
					<Loader v-if="loading"/>
					<button 
						@click="clearErrors()"
						class="btn btn--md" 
						type="button"
						data-dismiss="modal"
						aria-label="close"
						name="button">Cancel</button>
					<button 
						@click.prevent="createResponse"
						class="btn btn--md btn__primary"
						type="submit" 
						name="button">
						Save Response</button>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
import 'quill/dist/quill.core.css';
import 'quill/dist/quill.snow.css';
import 'quill/dist/quill.bubble.css';
import { quillEditor } from 'vue-quill-editor';
import form from '../../mixins/form';
import { createTestResponse } from '../../utils/api/quiz';
import quiz from '../../utils/validations/quiz';
import { fail, success } from '../../utils/toast';
import eventHub from '../../utils/eventhub';
import Loader from '../../components/Loader';

export default {
	name: 'AddResponse',
	mixins: [form],
	data() {
		return {
			itemData: {},
			loading: false,
			errors: {},
			editorOption: {
				modules: {
					toolbar: [
						['bold', 'italic', 'underline', 'strike'],
						['blockquote', 'code-block'],
						[{ 'header': 1 }, { 'header': 2 }],
						[{ 'list': 'ordered' }, { 'list': 'bullet' }],
						[{ 'script': 'sub' }, { 'script': 'super' }],
						[{ 'indent': '-1' }, { 'indent': '+1' }],
						[{ 'direction': 'rtl' }],
						[{ 'size': ['small', false, 'large', 'huge'] }],
						[{ 'header': [1, 2, 3, 4, 5, 6, false] }],
						[{ 'font': [] }],
						[{ 'color': [] }, { 'background': [] }],
						[{ 'align': [] }],
						['clean'],
						['link', 'image', 'video']
					]
				}
			}
		};
	},
	methods: {
		createResponse() {
			this.validate(this.itemData, quiz.createResponse);
			if (Object.keys(this.issues).length) {
				this.errors = this.issues;
				return false;
			}
			this.loading = true;
			createTestResponse(this.itemData)
				.then((response) => {
					if (response.status === 201) {
						this.loading = false;
						this.itemData = {};
						eventHub.$emit('response-created', response.data);
						success({
							title: 'Success! A new response has been created'
						});
						return true;
					}
					return false;
				})
				.catch((error) => {
					this.loading = false;
					fail({
						title: 'Oops! Something went wrong. Pls try again.'
					});
					return error;
				});
			return true;
		}
	},
	components: {
		Loader,
		quillEditor
	},
	computed: {
		editor() {
			return this.$refs.myTextEditor.quill;
		}
	}
};
</script>

<style scoped>
.quill-editor {
    height: 20rem;
}
.modal__footer {
	margin-top: 7em;
}
</style>
