<template>
	<div>
		<template v-if="loadingResponse">
			<div class="loading">
				<Loader />
			</div>
		</template>
		<template v-else>
			<div class="main__header">
				<div class="main__primary">
					<h3 class="main__title">Test Responses</h3>
				</div>

				<div class="d-flex">
					<button class="btn btn__primary" type="button" name="button"
						data-toggle="modal" data-target="#responseModal">
						<span class="btn__icon">
							<svg width="24" height="13" fill="none" xmlns="http://www.w3.org/2000/svg"><path d="M8 5H5V2H3v3H0v2h3v3h2V7h3V5zm10 1c1.66 0 2.99-1.34 2.99-3S19.66 0 18 0c-.32 0-.63.05-.91.14.57.81.9 1.79.9 2.86 0 1.07-.34 2.04-.9 2.86.28.09.59.14.91.14zm-5 0c1.66 0 2.99-1.34 2.99-3S14.66 0 13 0c-1.66 0-3 1.34-3 3s1.34 3 3 3zm6.62 2.16C20.45 8.89 21 9.82 21 11v2h3v-2c0-1.54-2.37-2.49-4.38-2.84zM13 8c-2 0-6 1-6 3v2h12v-2c0-2-4-3-6-3z" fill="#fff"/></svg>
						</span>Add Response</button>
				</div>
			</div>
			
			<section class="main-grid__row contacts contacts--list">
				<div class="contacts--list__wrapper">
					<div class="contacts--list__item head">
						<div></div>
						<p>Question Response</p>
						<p>Report</p>
						<p>Action</p>
					</div>
					<div class="contacts__body">
						<template v-if="responseLoaded && responses.length">						
							<div v-for="response in responses"
								:key="response.id">
								<div class="contacts--list__item">
									<label class="check">
										<input type="checkbox">
										<div class="check-mark"></div>
									</label>
									<p>{{ response.response }}</p>
									<p class="info__address" v-html="response.report"></p>
									<div>
										<router-link
											:to="{ name: 'test-response-detail',
											params: { id: response.id }}"
											class="btn btn__primary">
											View Details
										</router-link>
									</div>
								</div>
							</div>
						</template>
						<template v-else-if="responseLoaded">
							<p>No question created</p>
						</template>
						<template v-else>
							<p>An error occurred while loading response, please try again.</p>
						</template>
					</div>
				</div>
			</section>
		</template>
		<Pagination 
			:options="{ limit, offset, total, baseUrl }"
			v-if="total > limit" />
		<AddResponse />
	</div>
</template>

<script>
import Loader from '../../components/Loader';
import { getTestResponses } from '../../utils/api/quiz';
import Pagination from '../../components/Pagination';
import AddResponse from './AddResponse';
import eventHub from '../../utils/eventhub';

export default {
	name: 'TestResponse',
	data() {
		return {
			loadingResponse: true,
			responseLoaded: false,
			responses: [],
			baseUrl: 'test/response',
			limit: 10,
			total: 0,
			offset: 0
		};
	},
	created() {
		this.limit = this.$route.query.limit ? this.$route.query.limit : this.limit;
		this.offset = this.$route.query.offset ? this.$route.query.offset : this.offset;
		this.getResponses();
		eventHub.$on('response-created', (payload) => {
			this.responses.unshift(payload);
		});
	},
	watch: {
		$route() {
			this.limit = this.$route.query.limit ? this.$route.query.limit : 10;
			this.offset = this.$route.query.offset ? this.$route.query.offset : 0;
			this.getResponses();
		}
	},
	methods: {
		getResponses() {
			getTestResponses(this.limit, this.offset)
				.then((response) => {
					if (response.status === 200) {
						this.responses = response.data.data;
						this.responseLoaded = true;
						return true;
					}
					this.responseLoaded = false;
					return false;
				})
				.catch((error) => {
					this.responseLoaded = false;
					return error;
				})
				.finally(() => {
					this.loadingResponse = false;
				});
			return false;
		}
	},
	components: {
		Loader,
		AddResponse,
		Pagination
	},
	filters: {
		capitalize(value) {
			if (!value) return '';
			const finalValue = value.toString();
			return finalValue.charAt(0).toUpperCase() + finalValue.slice(1);
		}
	}
};
</script>

<style lang="scss" scoped>
.loading {
	min-height: 80vh;
	display: flex;
	justify-content: center;
	align-items: center;
}
.contacts--list__item {
    display: grid;
    grid-template-columns: 35px 1fr 3fr 120px;
}

</style>
